$selectric-main-color: #ddd !default; // Color used in border
$selectric-secondary-color: #bbb !default; // Color used in button
$black: #000;
$primary-color: #6ab04c;
$box-shadow: 0 10px 30px rgb(0 0 0 / 7%);
$selectric-text-color: $black !default; // Color used in label
$selectric-bg-color: transparent !default; // Background color
$selectric-btn-bg-color: transparent !default; // Button background color
$selectric-height: 40px !default; // Outer height
$selectric-spacing: 10px !default; // Label left padding
$selectric-border-width: 1px !default; // Outer border width
$selectric-border-radius: 100px !default; // Border radius
$selectric-inner-height: $selectric-height - ($selectric-border-width * 2) !default; // Inner height
$selectric-font-size: 11px !default; // Font size
$selectric-main-color:      #DDD !default;                                              // Color used in border
$selectric-secondary-color: #BBB !default;                                              // Color used in button
$selectric-text-color:      $black !default;                                              // Color used in label
$selectric-bg-color:        transparent !default;                                           // Background color
$selectric-btn-bg-color:    transparent !default;                                           // Button background color
$selectric-height:          40px !default;                                              // Outer height
$selectric-spacing:         10px !default;                                              // Label left padding
$selectric-border-width:    1px !default;                                               // Outer border width
$selectric-border-radius:   100px !default;                                               // Border radius
$selectric-inner-height:    $selectric-height - ($selectric-border-width * 2) !default; // Inner height
$selectric-font-size:       11px !default;                                              // Font size

.selectric-wrapper {
	position: relative;
	cursor: pointer;
}

.selectric-responsive {
	width: 100%;
}

.selectric {
	border: $selectric-border-width solid #f6f6f6;
	border-radius: $selectric-border-radius;
	background: $selectric-bg-color;
	position: relative;
	overflow: hidden;
	text-transform: uppercase;
	font-weight: 700;


	.label {
		display: block;
		white-space: nowrap;
		overflow: hidden;
		text-overflow: ellipsis;
		margin: 0 55px 0 $selectric-spacing;
		font-size: $selectric-font-size;
		line-height: $selectric-inner-height;
		color: $selectric-text-color;
		height: $selectric-inner-height;
		user-select: none;
		padding-left: 30px;
	}

	.button {
		display: block;
		position: absolute;
		right: 0;
		top: 0;
		width: $selectric-inner-height + 4px;
		height: $selectric-inner-height;
		line-height: $selectric-inner-height;
		background-color: $selectric-btn-bg-color;
		color: $black;
		text-align: center;
		font: 0/0 a;
		*font: 20px/#{$selectric-inner-height} Lucida Sans Unicode, Arial Unicode MS, Arial;

		&:after {
			content: " ";
			position: absolute;
			top: 0;
			right: 0;
			bottom: 0;
			left: 0;
			margin: auto;
			width: 0;
			height: 0;
			border: 4px solid transparent;
			border-top-color: $black;
			border-bottom: none;
		}
	}
}

